import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import api from '@/common/vmeitime-http/'
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			currentpage: '1',
			loadModal: true,
			searchtxt: '',
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			cateId: 0, //已选三级分类id
			priceOrder: 'asc', //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			sort:0
		};
	},
	//下拉刷新
	onPullDownRefresh(){
		this.currentpage = 1;
		this.searchinput(true)
	},
	//加载更多
	onReachBottom(){
		// this.loadData();
	},
	methods: {
		searchinput(flag) {
			console.log(this.searchtxt)
			setTimeout(()=> {
				uni.showLoading({title: '正在加载'})
				let url = '/goods/list?keyword='+this.searchtxt+'&page=1&limit=100&sort=name&order='+this.priceOrder+'&categoryId=0'
				api.httpget(url).then((res) => {
					let respons = res.data;
					if(respons.errno == 0) {
						this.goodsList = respons.data.list;
					}
					this.loadModal = false
					uni.hideLoading()
					if (flag) uni.stopPullDownRefresh()
				}).catch((err) => {
					if (flag) uni.stopPullDownRefresh()
					uni.hideLoading()
				})
			},100)
		},
		//筛选点击
		tabClick(index){
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			this.sort = index
			if(index === 2){
				this.priceOrder = this.priceOrder === 'asc' ? 'desc': 'asc';
			}else{
				this.priceOrder = 'asc';
			}
			uni.pageScrollTo({
				duration: 300, scrollTop: 0
			})
			this.searchinput();
		},
		//详情
		navToDetailPage(item){
			let id = item.id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		stopPrevent(){}
	},
}
